import React, {
  useRef,
  useState,
  useEffect,
  Fragment,
  useCallback,
} from "react";
import Ttext from "../../components/common/Ttext";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import * as actions from "../../store/actions";
import {
  Box,
  Typography,
  Grid,
  Hidden,
  Tooltip,
  IconButton,
  Paper,
} from "@material-ui/core";
import Input from "../../components/Inputs/Input";
import SquareButton from "../../components/Buttons/SquareButton";
import { tableIcons } from "../../util/tableIcons";
import MaterialTable from "material-table";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import DeleteRoundedIcon from "@material-ui/icons/DeleteRounded";
import i18next from "../../i18n";
import IconTextNotice from "../../components/common/IconTextNotice";
import FormatListBulletedRoundedIcon from "@material-ui/icons/FormatListBulletedRounded";
import IosSwitch from "../../components/Inputs/IosSwitch";
import { arSA } from "date-fns/locale";
import TableSkeleton from "../../components/common/TableSkeleton";
import GeneralDialog from "../../components/common/GeneralDialog";
import PageError from "../../components/common/PageError";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="name" />,
    field: "title",
  },
  {
    title: <Ttext ns="attributes" text="sort_order" />,
    field: "sort_order",
    width: 120,
  },
];

const initialQuery = {
  q: "",
  page: 0,
  perPage: 10,
  sort: "date_added",
  direction: "desc",
};

let timeout;

const Attributes = ({ location }) => {
  const [query, setQuery] = useState(initialQuery);
  const [deleteDialog, setDeleteDialog] = useState({
    open: false,
    selected: null,
  });

  const attributes = useSelector((state) => state.attributes);
  const [mounted, setMounted] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();
  const hasPagination = attributes.attributes.pagination;

  const handleQuery = (value, validity, name) => {
    setQuery((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const fetchAttributes = useCallback(async () => {
    try {
      await dispatch(actions.getAttributes(query));
    } finally {
      setMounted(true);
    }
  }, [dispatch, query]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchAttributes();
    }, 500);
    return () => clearTimeout(timeout);
  }, [query, fetchAttributes]);

  const handleSwitchState = (data) => {
    const id = data.attribute_id;
    const status = data.status > 0 ? 0 : 1;
    dispatch(actions.switchAttributeStatus(id, status));
  };

  const handleTriggerDeleteDialog = (selected) => {
    setDeleteDialog({
      open: !deleteDialog.open,
      selected: selected,
    });
  };

  const handleDeleteAttribute = async () => {
    const id = deleteDialog.selected.attribute_id;
    await dispatch(actions.deleteAttribute(id));
    handleTriggerDeleteDialog();
  };

  return (
    <Fragment>
      <Box>
        <Typography variant="h4" className="py">
          {t("attributes:attributes_list")}
        </Typography>
        <Grid container spacing={2} alignItems="center" className="py">
          <Grid item sm={8} xs={12}>
            <Input
              label={t("common:searchhhh")}
              getResult={handleQuery}
              name="q"
              initialValue={query.q}
            />
          </Grid>
          <Hidden smDown>
            <Grid item md={2} />
          </Hidden>
          <Grid item md={2} sm={4} xs={12}>
            <Link to={`${location.pathname}/add`}>
              <SquareButton
                title={t("attributes:new_attribute")}
                wIcon={
                  <FormatListBulletedRoundedIcon
                    className="text-light"
                    fontSize="large"
                    style={{
                      transform:
                        i18next.dir(i18next.language) === "rtl"
                          ? "rotateZ(180deg)"
                          : "",
                    }}
                  />
                }
                py={0.6}
                px={0.8}
                type="primary"
              />
            </Link>
          </Grid>
        </Grid>
      </Box>
      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} />
        </Paper>
      ) : !attributes.loading && attributes.errors ? (
        <PageError
          text={attributes.errors.message || t("common:uncommon_err")}
          onTryAgain={fetchAttributes}
        />
      ) : (
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={attributes.attributes.attributes}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const attribute = props.data;
              return (
                <>
                  <Tooltip title={t("common:edit")}>
                    <Link
                      to={`${location.pathname}/edit/${attribute.attribute_id}`}
                    >
                      <IconButton>
                        <EditRoundedIcon color="secondary" />
                      </IconButton>
                    </Link>
                  </Tooltip>
                  <Tooltip title={t("common:delete")}>
                    <IconButton
                      onClick={() => handleTriggerDeleteDialog(attribute)}
                    >
                      <DeleteRoundedIcon color="error" />
                    </IconButton>
                  </Tooltip>
                  <IosSwitch
                    state={attribute.status}
                    onChange={() => handleSwitchState(attribute)}
                    name="status"
                    loading={attributes.itemLoading}
                  />
                </>
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: query.perPage,
            pageSizeOptions: [5, 10, 20, 50],
            thirdSortClick: false,
            showTitle: false,
          }}
          onChangeRowsPerPage={(size) => handleQuery(size, true, "perPage")}
          onOrderChange={(orderBy, direction) => {
            const sort = tableHeaders[orderBy].field;
            setQuery((prev) => {
              return {
                ...prev,
                sort: sort,
                direction: direction,
              };
            });
          }}
          totalCount={hasPagination ? hasPagination.totalCount : 0}
          page={hasPagination ? hasPagination.currentPage : 1}
          onChangePage={(page) => handleQuery(page, true, "page")}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: attributes.loading
                ? t("common:loadingggg")
                : !attributes.loading &&
                  !attributes.errors && (
                    <IconTextNotice
                      Icon={FormatListBulletedRoundedIcon}
                      text={t("attributes:no_attributes")}
                    />
                  ),
              dateTimePickerLocalization: i18next.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={attributes.loading}
        />
      )}
      <GeneralDialog
        dialogOpen={deleteDialog.open}
        confirmText={t("common:confirm")}
        title={t("common:confirm_delete")}
        onCancel={() => handleTriggerDeleteDialog()}
        onConfirm={handleDeleteAttribute}
        loading={attributes.itemLoading}
        disableBackdropClick
      />
    </Fragment>
  );
};

export default Attributes;
